<template lang="">
  <div v-if="this.$store.state.info">
    <div class="title-block">
      <h2 class='title'>Сортировка</h2>
      <div class="types">

        <button class="type" @click=' sortedRepositoryByName'>ИМЯ</button>
        <button class="type" @click=' sortedRepositoryByName'>ЗВЕЗДЫ</button>
        <button class="type" @click='sortedRepository'>ДАТА</button>
      </div>
    </div>
    
   <div class="sort">
    <div class="sort__item" v-for="repositori in repositories" :key="repositori.id">
      <div class="sort__item-info">
        <h2 sort__item-title>{{repositori.name}}</h2>
        <p class="sort__item_text">Кол-во звёзд: {{repositori.forks}}</p>
        <p class="sort__item_text">Дата добавления: {{repositori.created_at}}</p>

      </div>
      <a href="#" class="Info__left-visit sort-visit">ПОСЕТИТЬ</a>
    </div>
   </div>


  </div>
</template>






<script>
  import { mapActions, mapState } from 'vuex';
export default {
data() {
  return {
    ascedingOrder: true
  }
},
   
    computed: {
        ...mapState(['repositories']),
  
     
    
    },
    methods: {
 
      sortedRepository(){
      return this.repositories.sort((a,b) => b.created_at.localeCompare(a.created_at), )
      },
      sortedRepositoryByName(){
      return this.repositories.sort((a,b) => a.name.localeCompare(b.name)) 
   
      },
      sortedRepositoryBystar(){
      return this.repositories.sort((a,b) => a.forks - b.forks) 
   
      }
     
 

      
    }
}
</script>

