<template>
    <MainCurrent/>
   
    <div class="container">


        <nav class="nav">
    <input 
    type="text" 
    class="nav__search" 
    placeholder="Введите имя пользователя" 
    v-model="getUser"
    @keydown.enter="getInfo(getUser)">
    <button class="nav__search-button" @click="getInfo(getUser)">НАЙТИ</button>
  </nav>
    </div>

</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
import MainCurrent from './MainCurrent.vue';

    export default {
        components: { MainCurrent,  },
        computed:{
       ...mapState(['user']),
       getUser: {
    get(){ 
    return this.user
    },
    set(val){
       this.setUser(val)
    }
       }
        },
        methods: {
            ...mapMutations(['setUser']),
            ...mapActions(['getInfo']),
        },
    }
</script>

